import Dashboard from "@/app/page";
import { Store } from "lucide-react";
import { register } from "node:module";
import { create } from "zustand";
import { persist } from "zustand/middleware";

type ViewType =
  | "register"
  | "login"
  | "dashboard"
  | "reset-password"
  | "verify-email"
  | "update-profile"
  | "delete-account";

interface UserData {
  fullName?: string;
  email: string;
  password: string;
}

interface AuthState {
  view: ViewType;
  registeredUser: UserData | null;
  setView: (view: ViewType) => void;
  signup: (data: UserData) => void;
  login: (email: string, pass: string) => boolean;
  logout: () => void;
  alert: { message: string; type: "success" | "error" } | null;
  setAlert: (
    alert: { message: string; type: "success" | "error" } | null,
  ) => void;
}

export const useAuthStore = create<AuthState>()(
  persist(
    (set, get) => ({
      view: "register",
      registeredUser: null,
      setView: (newView) => set({ view: newView }),

      signup: (userData) =>
        set({
          registeredUser: userData,
          view: "login",
        }),

      login: (email, password) => {
        const user = get().registeredUser;
       return user !== null && user.email === email && user.password === password;
      },

      logout: () => set({ view: "login" }),

      alert: null,
      setAlert: (alert) => set({ alert }),
    }),
    {
      name: "auth-storage",
    },
  ),
);
